<template>
    <div class="pc-container">
        <loader :loading="loading" />
        <div class="pcoded-content">
            <div class="row">
                <div class="col-xl-3 col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <div class="row align-items-center m-l-0">
                                <div class="col-auto">
                                    <i class="fas fa-list-check f-36 text-info"></i>
                                </div>
                                <div class="col-auto">
                                    <h6 class="text-muted m-b-10">Tareas</h6>
                                    <h2 class="m-b-0">{{ counts.alerts }}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-xl-3 col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <div class="row align-items-center m-l-0">
                                <div class="col-auto">
                                    <i class="fas fa-gavel f-36 text-danger"></i>
                                </div>
                                <div class="col-auto">
                                    <h6 class="text-muted m-b-10">Audiencias</h6>
                                    <h2 class="m-b-0">{{ counts.audiences }}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-xl-3 col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <div class="row align-items-center m-l-0">
                                <div class="col-auto">
                                    <i class="fas fa-calendar-days f-36 text-success"></i>
                                </div>
                                <div class="col-auto">
                                    <h6 class="text-muted m-b-10">Calendario</h6>
                                    <h2 class="m-b-0">{{ counts.audiences + counts.alerts }}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-xl-3 col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <div class="row align-items-center m-l-0">
                                <div class="col-auto">
                                    <i class="fas fa-layer-group f-36 text-primary"></i>
                                </div>
                                <div class="col-auto">
                                    <h6 class="text-muted m-b-10">Expedientes</h6>
                                    <h2 class="m-b-0">{{ counts.expTotal }}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            <div class="row">
                <!-- [ basic-table ] start -->
                <div class="col-xl-6 col-md-12">
                    <div class="card feed-card">
                        <div class="card-header">
                            <h5>Ultimas acciones</h5>
                        </div>
                        <div class="feed-scroll" style="height:350px;">
                            <div class="card-body">
                                <div class="row m-b-25 align-items-center" v-for="(item, index) in acciones"
                                    :key="index">
                                    <div class="col-auto p-r-0">
                                        <i class="fa-solid fa-bell bg-light-primary feed-icon p-2 wid-30 hei-30"></i>
                                    </div>
                                    <div class="col">
                                        <a href="javascript:void(0)">
                                            <h6 class="m-b-5">{{ accion(item) }}<span
                                                    class="text-muted float-right f-14">{{
                                                        item.user.name }} : {{
                                                        formatFecha(item.created_at)
                                                    }}</span></h6>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- [ basic-table ] end -->
                <!-- [ inverse-table ] start -->
                <div class="col-xl-6 col-md-12">
                    <div class="card table-card">
                        <div class="card-header">
                            <h5>Ultimos Expedientes Registrados</h5>
                        </div>
                        <div class="pro-scroll" style="height:350px;">
                            <div class="card-body p-0">
                                <div class="table-responsive">
                                    <table class="table table-hover m-b-0">
                                        <thead>
                                            <tr>
                                                <th style="font-size: 12px">Numero</th>
                                                <th style="font-size: 12px">Registro</th>
                                                <th style="font-size: 12px">Estado</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item, index) in expedientes" :key="index">
                                                <td class="text-muted" style="font-size: 11px">{{ item.exp_numero }}
                                                </td>
                                                <td class="text-muted" style="font-size: 11px">{{
                                                    formatFecha(item.created_at) }}
                                                </td>
                                                <td>
                                                    <div>
                                                        <label class="badge"
                                                            :class="item.exp_estado_proceso === 'EN TRAMITE' ? 'bg-light-warning' : 'bg-light-success'"
                                                            style="font-size: 10px; text-transform: capitalize !important;">
                                                            {{ item.exp_estado_proceso }}
                                                        </label>
                                                    </div>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Loader from '@/components/Loader/Loader.vue';
import feather from 'feather-icons';
import axios from '@/plugins/axios.js';

export default {
    name: 'admin',
    components: {
        Loader
    },
    data() {
        return {
            acciones: [],
            expedientes: [],
            counts: [],
            loading: false
        };
    },
    mounted() {
        this.cargarAcciones();
        this.$nextTick(() => {
            feather.replace();
        });
    },
    methods: {
        async cargarAcciones() {
            try {
                this.$showLoadingAlert();
                const response = await this.$postData('reportes/inicioadmin', this);
                this.acciones = response.audit;
                this.expedientes = response.proceeding;
                this.counts = response.count;
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'leer');
            } finally {
                this.$closeLoadingAlert();
            }
        },
        accion(item) {
            return item.accion == 'Revisó el Expediente' ? `${this.formatAccion(item.accion)} : ${item.exp.exp_numero}` : this.formatAccion(item.accion);
        },
        formatAccion(accion) {
            if (accion && typeof accion === 'string') {
                return accion.charAt(0).toUpperCase() + accion.slice(1).toLowerCase();
            } else {
                return accion;
            }
        },
        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                // hour: 'numeric',
                // minute: 'numeric',
                // second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        }
    },
};
</script>